.footer {
  padding-top: 29px;
  padding-bottom: 29px;
  background-color: #f7f7f7;
  //margin: 0 auto;
  @include desktop {
    min-width: 1280px;
  }
}
.footer__container {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
  height: 100%;
  @include tablet {
    flex-direction: row;
    justify-content: center;
  }
}
.footer__first-text-box {
  margin-bottom: 10px;
  display: flex;
  @media screen and (min-width: 768px) {
    margin-bottom: 0;
  }
}
.footer__second-text-box {
  display: flex;
  align-items: center;
}
.footer__text {
  display: block;
  font-size: 14px;
  line-height: 1.14;
  color: #545454;
  @media screen and (min-width: 768px) {
    font-size: 16px;
    line-height: 1.19;
  }
}

.footer__text--vertical-line {
  &::after {
    content: '|';
    margin: 0 5px;
  }
}

.footer__link {
  text-decoration-line: underline;
  margin-left: 4px;
}

.footer__svg {
  fill: var(--accent);
  width: 14px;
  height: 14px;
  margin: 0 10px;
  display: flex;
  align-self: center;
  animation: heartbeat 1.2s infinite;
}
@keyframes heartbeat {
  0% {
    transform: scale(1);
  }
  10% {
    transform: scale(1.4);
  }
  50% {
    transform: scale(1);
  }
  100% {
    transform: scale(1.2);
  }
}

/* -------------Modal Footer-------------- */
.basicLightbox {
  overflow: scroll;
}
.modal-team {
  display: flex;
  flex-wrap: wrap;
  padding: 10px;
  width: 240px;
  background-color: #f7f7f7;
  border-radius: 15px;
  @include tablet{
    width: 600px;
    border-radius: 25px;
  }
  @include desktop{
    margin: -30px;
    width: 800px;
  }
}
.team-card {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 20px;
  border-radius: 10px;
  width: calc((100% - 80px) / 2);
  box-shadow: rgba(50, 50, 93, 0.25) 0px 50px 100px -20px, rgba(0, 0, 0, 0.3) 0px 30px 60px -30px, rgba(10, 37, 64, 0.35) 0px -2px 6px 0px inset;
  transition: box-shadow 350ms cubic-bezier(0.4, 0, 0.2, 1);
  transform: scale(1);
  transition: transform 350ms cubic-bezier(0.4, 0, 0.2, 1);
  &:hover {
  box-shadow: rgba(250, 95, 5, 0.25) 0px 50px 100px -20px, rgba(218, 22, 22, 0.3) 0px 30px 60px -30px, rgba(247, 30, 30, 0.35) 0px -2px 6px 0px inset;
  transform: scale(1.15);
  }
  @include tablet {
    width: calc((100% - 160px) / 4);
    border-radius: 20px;
  }
  @include desktop {
    margin: 30px;
    border-radius: 30px;
    width: calc((100% - 240px) / 4);
  }
}
.team-name{
  padding: 5px;
  font-family: Roboto;
  font-size: 15px;
  font-style: oblique;
  @include tablet{
    font-weight: 500;
    font-size: 15px;
  }
  @include desktop {
    font-weight: 700;
    font-size: 20px;
  }
}
.team-role {
  font-family: Roboto;
  font-weight: 400;
  font-size: 10px;
  font-style: oblique;
  color: var(--accent);
  margin-bottom: 10px;
  @include tablet{
    font-weight: 500;
    font-size: 15px;
  }
  @include desktop {
    font-size: 15px;
  }
}
.team-git {
  margin-top: 10px;
  padding: 10px;
}
.icon-github {
  fill: var(--mainBlack);
  transform: scale(1);
  transition: transform 350ms cubic-bezier(0.4, 0, 0.2, 1), fill 350ms cubic-bezier(0.4, 0, 0.2, 1);
  &:hover,
  &:focus {
  fill: var(--accent);
  transform: scale(1.5);
  }
}
.title-team{
  position: absolute;
  top: 47%;
  right: 10%;
  font-family: 'Inter';
  font-style: italic;
  font-weight: 524;
  font-size: 42px;
  line-height: 39px;
  color: rgba(158, 157, 157, 0.767);
  font-variation-settings: 'slnt' -10;
  text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  @include tablet {
    top: 45%;
    right: 35%;
  }
  @include desktop {
    top: 44%;
    right: 37%;
  }
}